.calc{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
}

.container{
    background-color: #080808;
    box-shadow: 0px 4px 8px rgba(255, 255, 255, 0.5); 
    display: flex;
    flex-direction: column;
    width: 320px;
    border-radius: 20px;
}

h1{
    text-align: center;
    margin-top: 20px;
    color: white;
}

.header{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
}

.display{
    background: rgba(255, 255, 255, 0.8);
    width: 270px;
    display: grid;
    align-items: center;
    text-align: right;
    height: 50px;
    border-radius: 10px;
    margin-top: 30px;
    font-size: 20px;
    float: right;
    padding-right: 10px;
    border: none;
    outline: none;
    letter-spacing: 2px;
}

.numbers{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    margin: auto;
    margin-top: 20px;
    margin-bottom: 30px;
    gap: 10px;
}

.submit{
    background-color: #36454F;
    border-radius: 10px;
    width: 60px;
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    font-size: 20px;
    font-weight: 200;
    cursor: pointer;
}

.clr-back{
    display: grid;
    grid-template-columns: 2fr 2fr;
    margin: auto;
    margin-top: 20px;
    gap: 25px;
}

.clear, .backspace{
    background-color: #36454F;
    border-radius: 10px;
    width: 120px;
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    font-size: 20px;
    font-weight: 200;
    cursor: pointer;
}